.app__konux {
    width : 50%;
    margin : auto;

    &__running {
        align-items: center;
        display: flex;
        flex-direction: column;
        justify-content: center;
        min-height: 20vh;
    }

    .konux__loader {
        margin : auto;
        border: 16px solid #f3f3f3; /* Light grey */
        border-top: 16px solid #3498db; /* Blue */
        border-radius: 50%;
        width: 120px;
        height: 120px;
        animation: spin 2s linear infinite;
    }
    @keyframes spin {
        0% { transform: rotate(0deg); }
        100% { transform: rotate(360deg); }
    }
}



