.main-enter, .main-exit { left: 50%; position: absolute; top: 7rem; width: 100%; will-change: opacity, transform; }

@media only screen and (min-width: 1024px) { .main-enter, .main-exit { top: 7rem; } }

.main-enter { opacity: 0.01; transform: translate3d(-50%, 0, 0); z-index: 500; }

.main-enter.main-enter-active { opacity: 1; transform: translate3d(-50%, 0, 0); transition: opacity 0.2s 0.3s, transform 0.3s 0.2s ease-in-out; will-change: opacity, transform; }

.main-exit { opacity: 1; transform: translate3d(-50%, 0, 0); z-index: 250; }

.main-exit.main-exit-active { opacity: 0.01; transform: translate3d(-50%, 0, 0); transition: opacity 0.3s, transform 0.2s ease-in-out; }

.main-appear { opacity: 0.01; }

.main-appear.main-appear-active { opacity: 1; transition: opacity 0.5s ease-in; }

.transition-slide-up-appear, .transition-slide-up-enter { opacity: 0.01; transform: translate3d(0, 100%, 0); }

.transition-slide-up-appear.transition-slide-up-appear-active { opacity: 1; transform: translate3d(0, 0, 0); transition: 0.3s opacity, 0.3s transform cubic-bezier(0.35, 0.01, 0.77, 0.34); }

.transition-slide-up-enter.transition-slide-up-enter-active { opacity: 1; transform: translate3d(0, 0, 0); transition: 0.3s opacity, 0.3s transform cubic-bezier(0.35, 0.01, 0.77, 0.34); }

.transition-slide-up-exit { opacity: 1; transform: translate3d(0, 0, 0); }

.transition-slide-up-exit.transition-slide-up-exit-active { opacity: 0.01; transform: translate3d(0, 100%, 0); transition: 0.3s opacity, 0.3s transform cubic-bezier(0.35, 0.01, 0.77, 0.34); }

.transition-slide-down-appear, .transition-slide-down-enter { opacity: 0.01; transform: translate3d(0, -100%, 0); }

.transition-slide-down-appear.transition-slide-down-appear-active { opacity: 1; transform: translate3d(0, 0, 0); transition: 0.3s opacity, 0.3s transform cubic-bezier(0.35, 0.01, 0.77, 0.34); }

