@font-face {
    font-family: "icon-font-v3";
    font-style: normal;
    font-weight: normal;
    src: url("#{$font-path}icon-font-v3.ttf?hiqvyh") format("truetype"),
        url("#{$font-path}icon-font-v3.woff?hiqvyh") format("woff"),
        url("#{$font-path}icon-font-v3.svg?hiqvyh#icon-font-v3") format("svg");
}

[class^="i-"],
[class*=" i-"] {
    /* use !important to prevent issues with browser extensions that change fonts */
    display: inline-block;
    font-family: "icon-font-v3" !important; /* stylelint-disable font-family-no-missing-generic-family-keyword */
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
    font-style: normal;
    font-variant: normal;
    font-weight: normal;
    line-height: 1;
    speak: none;
    text-transform: none;
}

// *** Animations *** //
.i-spin {
    animation: rotate 2s infinite linear;
}

.i-fade {
    animation: fadeLoop 1.2s infinite linear;
}

.i-pulse {
    animation: pulse 1.2s infinite linear;
}

// Rotated & Flipped Icons

.i-rotate-90 {
    transform: rotate(90deg);
}

.i-rotate-180 {
    transform: rotate(180deg);
}

.i-rotate-270 {
    transform: rotate(270deg);
}

.i-flip-horizontal {
    transform: scale(-1, 1);
}

.i-flip-vertical {
    transform: scale(1, -1);
}

// Hook for IE8-9

:root .i-rotate-90,
:root .i-rotate-180,
:root .i-rotate-270,
:root .i-flip-horizontal,
:root .i-flip-vertical {
    filter: none;
}

